export default {
  mounted() {
    // init

    let stack = document.getElementById("stack");
    let stack_container = document.querySelector(".shirt-stack-container");
    let text_blink = document.querySelector(".shirt-stack-bottom");

    document.getElementById("Supreme").style.fill = "#190dfe";

    this.blinkInterval = setInterval(function() {
      text_blink.firstElementChild.classList.toggle("outlined");
    }, 800);

    this.swapInterval = setInterval(function() {
      swap();
    }, 3000);

    stack_container.addEventListener("click", swap);

    function swap() {
      let _cards = [...stack.children];
      let _firstCard = document.querySelector(".card1");

      cardAnim(_firstCard, 600);

      cardChange(_cards);
    }

    // shirt swap animation
    function cardAnim(card, duration) {
      card.animate(
        [
          // keyframes
          {
            transform: "translate(0, -350%)",
            zIndex: "410"
          }
        ],
        {
          // timing options
          duration: duration,
          // easing: "cubic-bezier(.17,.67,.9,.34)",
          easing: "cubic-bezier(.2,.54,.73,.25)",

          iterations: 2,
          direction: "alternate",
          fill: "both"
        }
      );
    }

    // swap each shirt element's css class according to the order in shirts stack
    function cardChange(cards) {
      cards.map(function(card) {
        if (card.classList.contains("card1")) {
          card.className = "card card10 card-backward";
          logoChange(card);
        } else if (card.classList.contains("card2")) {
          card.className = "card card1";
          card.lastElementChild.className = "shirt-overlay hide";
        } else if (card.classList.contains("card3")) {
          card.className = "card card2";
          card.lastElementChild.classList.remove("hide");
        } else if (card.classList.contains("card4")) {
          card.className = "card card3";
          card.lastElementChild.classList.remove("hide");
        } else if (card.classList.contains("card5")) {
          card.className = "card card4";
          card.lastElementChild.classList.remove("hide");
        } else if (card.classList.contains("card6")) {
          card.className = "card card5";
          card.lastElementChild.classList.remove("hide");
        } else if (card.classList.contains("card7")) {
          card.className = "card card6";
          card.lastElementChild.classList.remove("hide");
        } else if (card.classList.contains("card8")) {
          card.className = "card card7";
          card.lastElementChild.classList.remove("hide");
        } else if (card.classList.contains("card9")) {
          card.className = "card card8";
          card.lastElementChild.classList.remove("hide");
        } else if (card.classList.contains("card10")) {
          card.className = "card card9";
          card.lastElementChild.classList.remove("hide");
        }
      });

      // change color of brand logos on sidebar
      function logoChange(card) {
        var nodes = document.getElementById("brandlogos").childNodes;
        nodes.forEach(function(node) {
          if (node.nodeName.toLowerCase() == "path") {
            node.style.fill = "black";
          }
        });

        if (card.innerHTML.indexOf("shirt1") != -1) {
          document.getElementById("kith_1_").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt2") != -1) {
          document.getElementById("North-Face").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt3") != -1) {
          document.getElementById("adidas").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt4") != -1) {
          document.getElementById("stussy").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt5") != -1) {
          document.getElementById("BAPE").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt6") != -1) {
          document.getElementById("palace").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt7") != -1) {
          document.getElementById("kith").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt8") != -1) {
          document.getElementById("chinatownmarket").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt9") != -1) {
          document.getElementById("offwhite").style.fill = "#190dfe";
        }
        if (card.innerHTML.indexOf("shirt10") != -1) {
          document.getElementById("Supreme").style.fill = "#190dfe";
          document.getElementById("kith_1_").style.fill = "black";
        }
      }

      setTimeout(() => {
        document
          .querySelector(".card-backward")
          .lastElementChild.classList.remove("hide");
        document
          .querySelector(".card-backward")
          .classList.remove("card-backward");
      }, 800);
    }
  },
  beforeDestroy() {
    clearInterval(this.swapInterval);
  }
};
